.table {
	margin-top: 20px;
	overflow: scroll;
	overflow-x: hidden;
	height: 400px;
	color: #6a5d5d;
	margin-bottom: 15px;
}

.table tr {
	display: flex;
	justify-content: space-between;
}

.table td {
	padding: 0.5rem;
}

.table tr:nth-of-type(odd) {
	background-color: #f3f2f8;
}
